<template>
  <div class="c-page c-page--main">
    <edit-layout :assets="assets" :slot1="slot1" :slot2="slot2">
      <div class="l-page__footer">
        <button class="c-button c-button--primary c-button--huge" @click="$refs.popup.open()">Edit layout</button>
      </div>
    </edit-layout>
    <popup-edit-layout ref="popup" :assets="assets" :slot1="slot1" :slot2="slot2"/>
  </div>
</template>

<script>
  import EditLayout from '../components/EditLayout'
  import PopupEditLayout from '../components/PopupEditLayout.vue'

  export default {
    components: {
      PopupEditLayout,
      EditLayout},
    data () {
      return {
        assets: [{
          id: 1,
          type: 'smallImage',
          style: {
            backgroundImage: 'url(data:image/png;base64,)',
            maxHeight: '50px'
          }
        }, {
          id: 2,
          type: 'smallImage',
          style: {
            backgroundImage: 'url(data:image/png;base64,)',
            maxHeight: '50px'
          }
        }, {
          id: 3,
          type: 'bigImage',
          style: {
            backgroundImage: 'url(data:image/png;base64,)',
            maxHeight: '150px'
          }
        }, {
          id: 4,
          type: 'bigImage',
          style: {
            backgroundImage: 'url(data:image/png;base64,)',
            maxHeight: '150px'
          }
        }, {
          id: 5,
          type: 'bigImage',
          style: {
            backgroundImage: 'url(data:image/png;base64,)',
            maxHeight: '150px'
          }
        }],
        slot1: [],
        slot2: []
      }
    }
  }
</script>

<style lang="scss" scoped>
  .c-page--main {
    height: 100%;
    // similar to popup label height
    padding-top: rem(80);
  }

  .l-page__footer {
    //
  }
</style>



